import React, { Component } from 'react';
import './spotheatmap.css';

import SyncLoader from 'react-spinners/SyncLoader';

const {getHeatMapData} = require('../model/spotprices');

const lerpColor = (r1, g1, b1, r2, g2, b2) => {
  return (frac) => {
    const c1 = 1 - frac;
    const c2 = frac;
    return `rgb(${Math.round(c1 * r1 + c2 * r2)}, ${Math.round(c1 * g1 + c2 * g2)}, ${Math.round(c1 * b1 + c2 * b2)})`;
  };
}

// spot heat map
class SpotHeatMap extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: true,
      data: null,
      percentileThreshold: props.percentileThreshold,
    };
  }

  componentDidMount() {
    this.componentDidUpdate();
  }

  componentDidUpdate() {
    if (
        this.props.startDate !== this.state.startDate || 
        this.props.stopDate !== this.state.stopDate || 
        this.props.percentileThreshold !== this.state.percentileThreshold
    ) {
      // if we determine that what has changed is the date range, then we should fetch the data anew

      this.setState({
        loading: true,
        data: null,
        startDate: this.props.startDate,
        stopDate: this.props.stopDate,
        percentileThreshold: this.props.percentileThreshold
      });
  
      const props = this.props;
      
      // load the data for the heat map
      (async () => {
        const data = await getHeatMapData(props.startDate, props.stopDate);
        
        this.setState({
          "loading": false,
          "data": data,
        });
      })();
    }
  }

  updateColorThresholds() {
    const values = [];
    const datapoints = this.state.data.heatmap;
    for (const row of datapoints) {
      for (const val of row) {
        values.push(val);
      }
    }

    // clever code for picking pUpper and pLower such that 
    // when we pick colors, the colors are nice & accurate of the rough range
    // of the values 
    let pLower = -1;
    let pUpper = 1;

    const valuesBelowZero = values.filter((val) => {
      return val < 0;
    });
    const valuesAboveZero = values.filter((val) => {
      return val > 0;
    })

    const threshold = Math.min(Math.max(this.state.percentileThreshold / 100.0, 0), 1);

    if (valuesAboveZero.length > 0) {
      valuesAboveZero.sort();
      pUpper = valuesAboveZero[Math.floor(valuesAboveZero.length * (1 - threshold))];
    }

    if (valuesBelowZero.length > 0) {
      valuesBelowZero.sort();
      pLower = values[Math.floor(valuesBelowZero.length * threshold)];
    }

    const magMax = Math.max(pUpper, -pLower);
    this.pUpper = magMax;
    this.pLower = -magMax;
  }

  render() {
    if (this.state.loading) {
      // return <GridLoader loading={true} color={'#415EFF'}></GridLoader>
      return <SyncLoader loading={true} color={'#415EFF'}></SyncLoader>
    } else {
      this.updateColorThresholds();

      const pUpper = this.pUpper;
      const pLower = this.pLower;
      
      const rows = this.state.data.heatmap.map((row, index) => {
        return (
          <tr>
            <td><strong>{this.state.data.yLabels[index]}</strong></td>
            {row.map((value) => {
              let color;
              if (value < 0) {
                color = lerpColor(255, 255, 255, 0, 0, 255)(value / pLower);
              } else {
                color = lerpColor(255, 255, 255, 255, 0, 0)(value / pUpper);
              }
              if (value == null) 
                return (<td></td>);
              return (<td style={{'backgroundColor': color}}>${Math.round(value * 1000) / 1000}</td>);
            })}
          </tr>
        )
      });

      const header = this.state.data.xLabels.map((label) => {
        return (
          <th key={"header:" + label}>{label}</th>
        )
      });

      return (
        <table className="heatmap">
          <tbody>
            <tr>
              <th></th>
              {header}
            </tr>
            {rows}
          </tbody>
        </table>
      );

    }
  }
}

export default SpotHeatMap;