import React, { Component } from 'react';
import './App.css';

// bring in the date picker components
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

import SpotHeatMap from "./components/spotheatmap";

class App extends Component {
  constructor(props) {
    super(props);

    const startDate = new Date();
    const stopDate = new Date();
    startDate.setDate(stopDate.getDate() - 365);

    this.state = {
      "startDate": null,
      "stopDate": null,
      "percentileThreshold": 8
    }
  }

  changeStartDate(startDate) {
    if (!startDate) return ;

    const state = Object.assign({}, this.state);
    state.startDate = startDate;
    this.setState(state);
  }

  changeStopDate(stopDate) {
    if (!stopDate) return ;
    const state = Object.assign({}, this.state);
    state.stopDate = stopDate;
    this.setState(state);
  }

  changePercentileThreshold(event) {
    const state = Object.assign({}, this.state);
    state.percentileThreshold = event.target.value;
    this.setState(state);
  }

  render() {
    // create the heatmap only if the dates are valid
    let heatmap = null;
    if (this.state.startDate != null && this.state.stopDate != null) {
      heatmap = (
        <center style={{margin: '20px'}}>
          <SpotHeatMap
            startDate={this.state.startDate}
            stopDate={this.state.stopDate}
            percentileThreshold={this.state.percentileThreshold}
            />
        </center>
      )
    }

    // create the instructions
    const instructions = (
      <div style={{'textAlign': 'left'}}>
        <p>
          This <strong>spot price visualizer</strong> is a tool for exploring both long and short
          term trends in the prices of instances on the <a href="https://docs.aws.amazon.com/AWSEC2/latest/UserGuide/how-spot-instances-work.html">spot market</a>
        </p>
        <p>
          Usage:
        </p>
        <ul>
          <li>First, pick a start date for the range of dates you wish to examine by clicking the 'start date' date picker or by typing a value in the box</li>
          <li>Second, pick a stop date by clicking the 'stop date' date picker and entering your value</li>
          <li>Third, for advanced users, enter the percentile threshold which will be used to determine colors. Leave this as default if you don't know what this means.</li>
        </ul>
        <p>
          The spot price visualizer will then fetch the data for the prices on the 'start' and 'stop' dates and will display a heatmap visualization of 
          how the prices have changed between the two points in time. 
          The x axis at the top shows the az name and the y axis to the left shows the instance type.
          The values in the table are the changes in price in dollars. 
          Red colors represent an increase in price and blue colors represent a decrease in price. Some table entries are empty, these are az/insttype
          pairs for which no data is available as that instance type may not be offered in all regions.
        </p>
        <p>Note: Our archived data goes back to 2015.</p>
        <p>This project is made by Gareth George at UC Santa Barbaraâ€™s <a href="http://www.cs.ucsb.edu/~ckrintz/racelab.html">RACELab</a></p>
      </div>
    )

    return (
      <div className="App">
        <div className="content">
          <h1>AWS SpotPrice Visualizer</h1>
          <span>
            <strong>Start Date: </strong>
            <DatePicker
              selected={this.state.startDate}
              onChange={this.changeStartDate.bind(this)}
            />
          </span>

          <span style={{margin: '10px'}}>
            <strong>Stop Date: </strong>
            <DatePicker
              selected={this.state.stopDate}
              onChange={this.changeStopDate.bind(this)}
            />
          </span>

          <span style={{margin: '10px'}}>
            <strong>Percentile Threshold</strong>
            <input type="number" onChange={this.changePercentileThreshold.bind(this)}
              min="0" max="100" step="1" value={this.state.percentileThreshold}></input>
          </span>
          
          {heatmap}

          <hr></hr>
          {instructions}
        </div>
      </div>
    );
  }
}

export default App;
